body {
  color: #f5f3f4;
  text-align: center;
  background-color: #f5f3f4;
  font-family: "Press Start 2P", system-ui;
}

.game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#strip {
  height: 2px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

* {
  box-sizing: border-box;
}

header, article {
  background-color: #1a3f70;
}

header {
  border: 1px solid #000;
  padding: 15px 0;
}

article {
  float: left;
  text-align: center;
  color: #f5f3f4;
  border: 1px solid #1a3f70;
  border-radius: 25px;
  width: 20%;
  margin: 0% 1.5%;
  padding: 20px;
  font-size: 15px;
}

nav {
  float: right;
  width: 30%;
  margin: 0%;
  padding: 20px;
}

section:after {
  content: "";
  clear: both;
  display: table;
}

table, th, td {
  border-collapse: collapse;
  background: #1a3f70;
  border: 1px solid #f5f3f4;
}

table {
  width: 100%;
}

th, td {
  padding: 10px;
}

input {
  margin: 5px;
  padding: 4px;
}

td {
  font-size: 12px;
}

button:hover {
  background-color: #c3d9ea;
}

button {
  font-family: "Press Start 2P", system-ui;
  font-size: x-small;
}

input {
  width: 90%;
  font-family: "Press Start 2P", system-ui;
  font-size: x-small;
}

@media (width <= 900px) {
  article, nav {
    width: 100%;
    height: auto;
  }
}

.leaderboard, .buttonSet {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.headerAccent {
  color: #ef9500;
}
